.img{
    background-color: transparent;
    width: 3vw;
    height: 3vw;
}

.heading{
    font-size: calc(10+35vw);
    display: flex;
    justify-self: center;
    align-self: center;
    text-align: center;
}

.headingwrapper{
    display: flex;
    flex-basis: 25%;
    justify-content: center;
    align-items: center;
    background-color: grey;
    margin-top: 1vw;
    margin-bottom: 1vw;
    width: 100%;
    text-align: center;
}

p{
    margin-bottom: 0mm;
 }

.subheading{
    font-size: calc(10+15vw);
    display: flex;
    flex-basis: 10%;
    width: 100%;
    
}

.dateTitle{
    font-size: calc(10+2vw);
    font-style: italic;
}

.description{
    display: flex;
    flex-basis: 30%;;
    width: 75%;
    padding-top: 1vw;
    padding-bottom: 1vw;
    padding-left: 1vw;
    padding-right: 1vw;
    justify-items: center;
    align-self: center;
    text-align: center;

}

.txt{
    justify-self: center;
    align-items: center;  
}